/* eslint-disable no-undef */
import common from '@/common'
import browser from '@/tools/browser'
import system from '@/tools/system'
import {
  // eslint-disable-next-line no-unused-vars
  mapActions
} from 'vuex'

const jsApiList = [
  'checkJsApi',
  'chooseWXPay',
  'onMenuShareTimeline',
  'onMenuShareAppMessage',
  'onMenuShareQQ',
  'onMenuShareWeibo',
  'onMenuShareQZone',
  'hideMenuItems',
  'showMenuItems',
  'chooseImage',
  'previewImage',
  'uploadImage',
  'downloadImage'
]

const defaultShareParams = {
  title: '', // 
  desc: '2-6', // 
  // link: location.origin + '/?from=singlemessage&isappinstalled=0&_safari_back_err_fix=1', // 
  link: location.href,
  imgUrl: 'https://staticimg.ngmm365.com/5ca8495e7f596c5bc0af6c0ae7fdb763-w229_h227.png' // 
}

let wxShare = {
  methods: {
    // {apiList, shareParams, hideShareMenu, callback, errorCallback}
    wxSetShareSign (opt) {
      if (!system.isWechat()) { //  
        console.log('')
        return
      }

      defaultShareParams.link = location.href

      opt = opt || {}

      if (typeof opt === 'function') {
        opt = { callback: opt }
      }

      let self = this

      let appId = common.config.appId_yx

      let apiList = opt.apiList || jsApiList

      let shareParams = opt.shareParams ? Object.assign({}, defaultShareParams, opt.shareParams) : defaultShareParams

      let callback = opt.callback || null

      let errorCallback = opt.errorCallback || null

      let hideShareMenu = opt.hideShareMenu || false

      let signUrl = browser.isIphone /* && system.getWechatVersion() < '6.5.2' */ ? localStorage.getItem('wechatCurPage') : location.href

      let shareAppMessageSuccess = opt.shareAppMessageSuccess || null

      let shareTimelineSuccess = opt.shareTimelineSuccess || null

      // eslint-disable-next-line no-undef
      httpPost('/account/signature', {
        appId: appId,
        url: signUrl
      }, function (obj) {
        if (obj.code === 10000) {
          wx.config({
            debug: false,
            appId: obj.data.appId,
            timestamp: obj.data.timestamp,
            nonceStr: obj.data.nonceStr,
            signature: obj.data.signature,
            jsApiList: apiList
          })

          wx.error(function (res) {
            console.log('wx.config:error')
            console.log(res)
            errorCallback && errorCallback(res)
          })

          if (hideShareMenu) {
            self.wxHideAllMenu()
          } else {
            if (callback) {
              wx.ready(() => {
                callback()
              })
            } else {
              self.wxSetShareInfo(shareParams, shareAppMessageSuccess, shareTimelineSuccess)
            }
          }
        } else {
          self.actionVuexMessageShow(obj.desc || '')
        }
      })
    },
    wxSetShareInfo (shareParams, shareAppMessageSuccess, shareTimelineSuccess) {
      let self = this
      wx.ready(function () {
        wx.onMenuShareAppMessage({
          title: shareParams.title, // 
          desc: shareParams.desc, // 
          link: shareParams.link, // 
          imgUrl: shareParams.imgUrl, // 
          type: '', // ,musicvideolinklink
          dataUrl: '', // typemusicvideo
          success: function () {
            shareAppMessageSuccess && shareAppMessageSuccess()
            self.actionVuexMessageShow('!')
            console.log('')
          },
          cancel: function () {
            console.log('')
          }
        })

        wx.onMenuShareTimeline({
          title: shareParams.title, // 
          desc: shareParams.desc, // 
          link: shareParams.link, // 
          imgUrl: shareParams.imgUrl, // 
          type: '', // ,musicvideolinklink
          dataUrl: '', // typemusicvideo
          success: function () {
            shareTimelineSuccess && shareTimelineSuccess()
            self.actionVuexMessageShow('!')
            console.log('')
          },
          cancel: function () {
            console.log('')
          }
        })

        wx.hideMenuItems({
          menuList: [
            'menuItem:share:qq', // QQ
            'menuItem:share:QZone' // QQ
          ],
          success: function (res) {
            console.log('')
          },
          fail: function (res) {
            console.log(JSON.stringify(res))
          }
        })

        wx.showMenuItems({
          menuList: [
            'menuItem:share:appMessage',
            'menuItem:share:timeline',
            'menuItem:favorite',
            'menuItem:openWithQQBrowser',
            'menuItem:copyUrl'
          ], // menu3
          success: function (res) {
            console.log('')
          },
          fail: function (res) {
            console.log(JSON.stringify(res))
          }

        })
      })
    },
    wxHideAllMenu () {
      if (this.isApp) {
        NGJsBridge.showToolBar({ cart: false, share: false })
        return
      }
      wx.ready(function () {
        wx.hideMenuItems({
          menuList: [
            'menuItem:exposeArticle',
            'menuItem:setFont',
            'menuItem:dayMode',
            'menuItem:nightMode',
            'menuItem:refresh',
            'menuItem:profile',
            'menuItem:addContact',
            'menuItem:share:appMessage',
            'menuItem:share:timeline',
            'menuItem:share:qq',
            'menuItem:share:weiboApp',
            'menuItem:favorite',
            'menuItem:share:facebook',
            'menuItem:share:QZone',
            'menuItem:editTag',
            'menuItem:delete',
            'menuItem:copyUrl',
            'menuItem:originPage',
            'menuItem:readMode',
            'menuItem:openWithQQBrowser',
            'menuItem:openWithSafari',
            'menuItem:share:email',
            'menuItem:share:brand'
          ],
          fail: function (res) {
            console.log(JSON.stringify(res))
          }
        })
      })
    }
  }
}
export default wxShare
